import { Loader } from 'components/sheared';
import React from 'react';
import { Box, Card, CardContent, Divider, Typography } from '@mui/material';
import { red } from '@mui/material/colors';
import { ITrackPackageInfo } from 'redux/helpers/dataTypes';
interface PackageDataProps {
  packageData: Partial<ITrackPackageInfo>;
  isLoading: boolean;
}

const PackageData: React.FC<PackageDataProps> = ({
  packageData,
  isLoading,
}) => {
  const {
    WarehouseSender = '  ',
    WarehouseRecipient = '  ',
    FactualWeight = '0',
    ActualDeliveryDate = '-',
    DocumentCost = '0',
    Number,
    Status = '',
  } = packageData;

  return (
    <>
      {!isLoading ? (
        <>
          {Object.keys(packageData).length > 0 && (
            <>
              <Typography
                variant="h2"
                fontWeight={'700'}
                fontSize={36}
                textAlign="center"
                gutterBottom
              >
                  
              </Typography>
              <Box>
                <Card>
                  <CardContent>
                    <Box mb={3}>
                      <Typography variant="h5" color={red[500]} gutterBottom>
                        
                      </Typography>
                      <Typography variant="h5" component="p">
                        {WarehouseSender}
                      </Typography>
                      <Divider />
                    </Box>
                    <Box mb={3}>
                      <Typography variant="h5" color={red[500]} gutterBottom>
                        
                      </Typography>
                      <Typography variant="h5" component="p">
                        {WarehouseRecipient}
                      </Typography>
                      <Divider />
                    </Box>
                    <Typography variant="h5" color={red[500]} gutterBottom>
                      
                    </Typography>

                    <Typography variant="h5" component="p">
                      : {Number}
                    </Typography>

                    <Typography variant="h5" component="p">
                      : {Status}
                    </Typography>

                    <Typography variant="h5" component="p">
                       : {ActualDeliveryDate}
                    </Typography>

                    <Typography variant="h5" component="p">
                      : {DocumentCost} 
                    </Typography>

                    <Typography variant="h5" component="p">
                      : {FactualWeight} 
                    </Typography>
                  </CardContent>
                </Card>
              </Box>
            </>
          )}
        </>
      ) : (
        <Loader />
      )}
    </>
  );
};

export default PackageData;
